@import url("normalize.css");
@font-face {
  font-family: 'Bebas Neue';
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: local('Bebas Neue Book'), local('BebasNeueBook'), url('../fonts/bebasneuebook.woff2') format('woff2'), url('../fonts/bebasneuebook.woff.woff') format('woff');
}
@font-face {
  font-family: 'Bebas Neue';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local('Bebas Neue Regular'), local('BebasNeueRegular'), url('../fonts/bebasneueregular.woff2') format('woff2'), url('../fonts/bebasneueregular.woff') format('woff');
}
@font-face {
  font-family: 'Montserrat';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local('Montserrat Regular'), local('Montserrat-Regular'), url('../fonts/Montserrat-Regular.woff2') format('woff2'), url('../fonts/Montserrat-Regular.woff') format('woff');
}
@font-face {
  font-family: 'Montserrat';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local('Montserrat Medium'), local('Montserrat-Medium'), url('../fonts/Montserrat-Medium.woff2') format('woff2'), url('../fonts/Montserrat-Medium.woff') format('woff');
}
.page {
  height: 100%;
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.55555556;
  background-color: #fff;
  background-image: url("../img/deer@1x.png"), url("../img/background@1x.jpg");
  background-repeat: no-repeat;
  background-position: top left, top center;
  background-size: auto 100%, cover;
}
@media (min-resolution: 2dppx) {
  .page {
    background-image: url("../img/deer@2x.png"), url("../img/background@2x.jpg");
  }
}
.page__container {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}
.container {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1280px;
  }
}
@media (min-width: 1920px) {
  .container {
    width: 1860px;
  }
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  white-space: nowrap;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(100%);
  overflow: hidden;
}
.page__nav {
  flex-basis: 950px;
  flex-grow: 0;
  flex-shrink: 0;
}
.nav {
  padding-top: 60px;
  padding-bottom: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__item {
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Bebas Neue', 'Arial', sans-serif;
  font-size: 24px;
  color: #1d1d1b;
}
.nav__item--tel {
  display: block;
  border: 1px solid #c7af8a;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 13px;
  padding-right: 13px;
  border-radius: 10px;
}
.main__wrapper {
  flex-basis: 1010px;
  flex-grow: 0;
  flex-shrink: 1;
}
.main__title {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  padding-right: 250px;
  padding-bottom: 85px;
  font-family: 'Bebas Neue', 'Arial', sans-serif;
  color: #444444;
}
.main__subtitle {
  padding-left: 60px;
  font-size: 34px;
}
.main__slogan {
  align-self: flex-end;
  font-size: 51px;
  font-weight: 300;
}
.offer {
  margin: 0 auto;
  width: 370px;
  font-family: 'Montserrat', 'Arial', sans-serif;
}
.main__price {
  color: #c7af8a;
  text-transform: lowercase;
  font-size: 16.42px;
}
